import Vue from 'vue'
import Router from 'vue-router'
import index from '../pages/index'
import upload from '../pages/upload'
import filterCom from '../pages/filter'
import mine from '../pages/mine'
import AddTarget from '../pages/AddTarget'
import SearchTarget from '../pages/SearchTarget'
import check from "../pages/check"
import calcIndex from "../pages/calcIndex"
import makeCalc from "../pages/makeCalc"
import calcResult from "../pages/calcResult"
import calcGroup from "../pages/calcGroup"
import mineSearch from '../components/search-list'
import Fail from '../components/fail'

Vue.use(Router)

var myRouter=new Router({
  routes: [
    {
      path: '/fail',
      name:'fail',
      component: Fail
    },
    {
      path: '/',
      redirect: '/index',
      meta: {
        keepAlive: false
      }
    }, {
      path: '/index',
      name:'index',
      component: index,
      meta: {
        keepAlive: false
      }
    }, {
      path: '/upload',
      component: upload,
      meta: {
        keepAlive: false
      }
    }, {
      path: '/filter',
      name:'filter',
      component: filterCom,
      meta: {
        keepAlive: true
      }
    }, {
      path: '/filter/add',
      component: AddTarget,
      meta: {
        keepAlive: true
      }
    }, {
      path: '/filter/search',
      name:'filterSearch',
      component: SearchTarget,
      meta: {
        keepAlive: true
      }
    },{
      path: '/mine',
      component: mine,
      meta: {
        keepAlive: true
      }
    },{
      path: '/mine/search',
      name:'search',
      component: mineSearch,
      meta: {
        keepAlive: true
      }
    },{
      path:'/check',
      name:'check',
      component:check
    },{
      path:'/calcIndex',
      name:'calcIndex',
      component:calcIndex
    },{
      path:'/calcResult',
      name:'calcResult',
      component:calcResult
    },
    {
      path:'/makeCalc',
      name:'makeCalc',
      component:makeCalc
    },
    {
      path:'/calcGroup',
      name:'calcGroup',
      component:calcGroup
    }
  ]
});
//统一控制需要缓存页面
myRouter.beforeEach((to, from, next) => {
  if (to.path == '/filter' && from.path == '/index') {//从首页跳转到filter修改meta为false
    to.meta.keepAlive = false;
  }
  if (to.path == '/filter' && from.path == '/mine') {//从我的用户群跳转到filter修改meta为false
    to.meta.keepAlive = false;
  }
  if (from.path == '/filter') {
    if(to.path=='/filter/add'){//如果是从filter跳转到add页面；将id传入add的路由，后面在传回来
      to.params.batchId=from.params.batchId;
      to.params.fileAddr=from.params.fileAddr;
    }
    from.meta.keepAlive = true;
  }

  //下面相当于filter页面的beforeRouteEnter
  if (to.path == '/filter') {
    isAddBack = false;
    isFilterClear = false;//默认不清缓存
    if (from.path == '/filter/add') {
      to.params.batchId=from.params.batchId;
      to.params.fileAddr=from.params.fileAddr;      //将id等参数传回filter页面。
      isAddBack = true;
    }
    if (to.meta.keepAlive == false) { // 不需要调用缓存
      //此处修改是否清空数据标识
      isFilterClear=true;
      //还原成true，为下一次操作做准备
      to.meta.keepAlive = true
    }
  }

  next()
})
export default myRouter



